<template>
    <nav class="nav flex-row-between">
        <img src="~@/assets/images/common/logo.png" class="nav-logo pointer" @click="togglePage('/')"/>
        <div class="nav-list flex-1 flex-row-start">
            <div 
                class="flex-row-center nav-list__item pointer" 
                v-for="item in navList" 
                :key="item.name"
                @click="togglePage(item.path)">
                <img :src="item.icon" class="nav-list__icon" />
                <span class="nav-list__text" :class="{'active': $route.path == item.path}">{{item.name}}</span>
            </div>
            <el-dropdown trigger="click">
                <div class="flex-row-center nav-list__item pointer">
                    <img src="~@/assets/images/common/more.png" class="nav-list__icon" />
                    <span class="nav-list__text">More</span>
                    <img src="~@/assets/images/common/arrow.png"  class="nav-list__arrow"/>
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item 
                            class="flex-row-start nav-list__item select pointer" 
                            v-for="item in moreList" 
                            :key="item.name"
                            @click="togglePage(item.path)">
                            <img :src="item.icon" class="nav-list__icon" />
                            <span class="nav-list__text" :class="{'active': $route.path == item.path}">{{item.name}}</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
        <div class="nav-right flex-row-end">
            <div class="nav-buy text-center">Buy</div>
            <div class="nav-money text-center flex-row-center">
                <img src="~@/assets/images/common/money.png" class="nav-money__icon"/>
                <span>100</span>
            </div>
            
            <el-dropdown trigger="click">
                <div class="flex-row-center nav-group">
                    <img src="~@/assets/images/common/group.png" class="nav-group__icon"/>
                    <img src="~@/assets/images/common/arrow.png" class="nav-group__arrow" />
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <img src="~@/assets/images/common/car.png" class="nav-group__list-icon"/>
                            <span>Buy TKB</span>
                        </el-dropdown-item>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <img src="~@/assets/images/common/refresh.png" class="nav-group__list-icon"/>
                            <span>Refresh</span>
                        </el-dropdown-item>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <img src="~@/assets/images/common/dis.png" class="nav-group__list-icon"/>
                            <span>Disconnect</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
        <TabBar class="nav-tabbar" :nav-list="navList" :more-list="moreList" @click="togglePage"/>
    </nav>
</template>

<script>
import TabBar from './tabbar.vue'
export default {
    components: {
        TabBar
    },
    data() {
        return {
            navList: [{
                name: 'Store',
                icon: require('@/assets/images/common/store.png'),
                path: '/store'
            },{
                name: 'Land',
                icon: require('@/assets/images/common/land.png'),
                path: '/land'
            },{
                name: 'Market',
                icon: require('@/assets/images/common/market.png'),
                path: '/market'
            }],
            moreList: [{
                name: 'Inventory',
                icon: require('@/assets/images/common/inventory.png'),
                path: '/inventory'
            },{
                name: 'Merit',
                icon: require('@/assets/images/common/merit.png'),
                path: '/merit'
            },{
                name: 'NFT BuyBack',
                icon: require('@/assets/images/common/buyBack.png')
            },{
                name: 'Stakes TKS',
                icon: require('@/assets/images/common/TKS.png')
            },{
                name: 'Documentation',
                icon: require('@/assets/images/common/documentation.png')
            }]
        }
    },
    mounted(){
        console.log(this.$route)
    },
    methods: {
        /**
         * 跳转到指定页面
         * @param {String} path 页面路由地址
         */
        togglePage(path) {
            if (!path) {
                return
            }
            this.$router.push({
                path
            })
        }
    }
}
</script>

<style lang="scss">
    @media only screen and (max-width: 768px) {
        .nav-list{
            display: none !important;
        }
    }

    @media only screen and (min-width: 768px){
        .nav-tabbar{
            display: none !important;
        }
    }
    .el-dropdown-menu{
        background: #1B1B1B !important;
    }
    .el-popper.is-light{
        border: none !important;
    }
    .nav-group__list{
        align-items: center !important;
    }
    .nav-group__list-icon{
        height: .24rem;
        margin-right: .2rem;
    }
    .el-dropdown__popper.el-popper[role=tooltip]{
        border-color: #8F8F8F !important;
    }
    .el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover{
        background: #86713D !important;
        color: #FCBF2F !important;
        .nav-list__text{
            color: #FCBF2F !important;
        }
    }
</style>
<style lang="scss" scoped>
.nav-list__text.active{
    color: #FCBF2F;
}
.nav{
    height: .8rem;
    padding: 0 .3rem;
    align-items: center;
    background: #181818;
    &-logo{
        width: 2.16rem;
        height: .4rem;
    }
    &-list{
        padding-left: .6rem;
    }
    &-list__item{
        margin-right: .4rem;
        &.select{
            align-items: center;
            margin-right: 0;
        }
    }
    &-list__icon{
        width: .2rem;
        height: .2rem;
        margin-right: .1rem;
    }
    &-list__arrow{
        width: .1rem;
        height: auto;
        margin-left: .1rem;
    }
    &-list__text{
        font-size: .18rem;
        color: #fff;
    }
    &-buy{
        width: .92rem;
        height: .36rem;
        line-height: .36rem;
        background: linear-gradient(-68deg, #FCC92F, #FB7A2F);
        border-radius: 18px;
        color: #fff;
        font-size: .18rem;
        margin-right: .2rem;
    }
    &-money{
        width: .92rem;
        height: .36rem;
        line-height: .36rem;
        border-radius: 18px;
        color: #fff;
        font-size: .18rem;
        background: #444;
        .nav-money__icon{
            width: .18rem;
            margin-right: .1rem;
        }
    }
    &-group__icon{
        width: .2rem;
        height: .2rem;
        margin-left: .27rem;
        margin-right: .06rem;
    }
    &-group__arrow{
        width: .08rem;
    }
    
}
</style>